.c-homepage {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  &:before {
    content: "";
    background-image: url("/assets/bg.jpg");
    background-size: cover;
    position: absolute;
    background-color: black;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: 1;
  }

  &__row {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;

    h2 {
      color: white;
      font-size: 35px;
      margin-bottom: 20px;

      @media only screen and (max-width: 768px) {
        font-size: 27px;
      }
    }
  }

  &__circle {
    position: absolute;
    top: 26%;
    left: 53%;
  }

  &__circle-item {
    position: relative;
    left: 56%;
    opacity: 1;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #fff;
    box-shadow: inset 0 0 30px 0 #fff, inset 20px 0 30px #ffd400,
      inset -20px 0 30px red, inset 20px 0 300px #ff40d6,
      inset -20px 0 300px #e3ff4b, 0 0 50px #fff, -10px 0 100px #fbff00,
      10px 0 80px #f55;

    animation: circleMove 3s infinite;
    animation-direction: alternate;
  }

  &__welcome {
    position: absolute;
    top: 55%;
    left: 200px;

    h1 {
      opacity: 1;
      transform: none;
      font-size: 25px;
      font-weight: 500;
      margin: 2rem 0rem 1.5rem;
      position: relative;
      text-transform: uppercase;
      letter-spacing: 9.9px;
      font-family: Blorado, sans-serif;
      text-rendering: geometricprecision;
      color: #ff9e8a;

      &::before {
        left: -2px;
        text-shadow: rgb(255, 105, 180) 3px 0px;
        animation: 2s linear 0s infinite alternate-reverse none running
          noiseanimbefore;
        content: attr(data-text);
        position: absolute;
        top: 0px;
        width: 100%;
        height: 100%;
      }

      &::after {
        left: 2px;
        text-shadow: rgb(255, 85, 85) -1px 0px;
        animation: 15s linear 0s infinite alternate-reverse none running
          noiseanimafter;
        content: attr(data-text);
        position: absolute;
        top: 0px;
        width: 100%;
        height: 100%;
      }
    }

    mark {
      color: inherit;
      padding: 0px;
      background: none 0px 80% / 100% 0.3em no-repeat;
      position: relative;
    }

    @media only screen and (max-width: 1200px) {
      left: 50px;
    }
    @media only screen and (max-width: 768px) {
      left: 5%;
    }
  }
  &__typical {
    position: absolute;
    top: 65%;
    left: 200px;

    @media only screen and (max-width: 1200px) {
      left: 50px;
    }
    @media only screen and (max-width: 768px) {
      left: 5%;
    }
  }

  .styles_typicalWrapper__1_Uvh {
    font-size: 30px;
    color: white;
    font-weight: bold;
    font-family: "Dancing Script", cursive;
    @media only screen and (max-width: 768px) {
      font-size: 30px;
    }
  }
}
